/* 订单API */
import request from '@/utils/request'

// 获取订单信息
export const getCheckOrder = (mode, obj) => {
  return request.get('/checkout/order', {
    params: {
      mode,
      delivery: 10,
      couponId: 0,
      isUsePoints: 0,
      ...obj
    }
  })
}

// 提交订单
export const submitOrder = (mode, params) => {
  return request.post('/checkout/submit', {
    mode,
    delivery: 10,
    couponId: 0,
    payType: 10,
    isUsePoints: 0,
    ...params
  })
}
// 获取我的订单列表
export const getMyOrderList = (dataType, page) => {
  return request.get('/order/list', {
    params: {
      dataType,
      page
    }
  })
}
// 取消订单
export const cancelMyOrder = (orderId) => {
  return request.post('/order/cancel', { orderId })
}

// 确认收货
export const receiptMyOrder = (orderId) => {
  return request.post('/order/receipt', { orderId })
}
